@import url(https://rsms.me/inter/inter.css);
html { font-family: 'Inter', sans-serif; }
@supports (font-variation-settings: normal) {
  html { font-family: 'Inter var', sans-serif; }
}

html,
body,
#root {
  height: 100%;
  color: var(--white);
}

body {
  margin: 0;
  padding: 0;
}

button,
input[type="submit"],
input[type="reset"] {
  background: none;
  color: inherit;
  border-width: 4px;
  border-color: var(--dark);
  border-left: 1;
  border-right: none;
  border-top: none;
  border-bottom: none;

  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
}

:root {
  --light: #565F69;
  --medium: #3C4650;
  --dark: #232D37;
  --darkest: #0B131D;
  --hilight: #FA1446;
  --highlight: 230, 38, 74;
  --white: white;

  /* Fonts */

  --xl: 2em;
  --l: 1.75em;
  --m: 1.25em;
  --s: 1em;
  --xs: 0.75em;


}

.App {
  height: 100%;
  color: white;
}

.App__Chat {
  display: -webkit-flex;
  display: flex;
  height: 100%;

}

.App--MenuOpen .App__ConversationList {
  width: 100%;
}
.App--MenuClosed .App__ConversationList {
  width: 0%;
  overflow: hidden;
}

.App--MenuOpen .App__Conversation {
  width: 0%;
}

.App--MenuClosed .App__Conversation {
  width: 100%;
}


@media screen and (min-width: 760px) {
  .App--MenuOpen .App__ConversationList,
  .App--MenuClosed .App__ConversationList {
    width: 30%;
    overflow: auto;
  }
  .App--MenuOpen .App__Conversation,
  .App--MenuClosed .App__Conversation {
    width: 70%;
  }
}

/* SCROLLBAR */
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #232D37;
  background: var(--dark);
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #3C4650;
  background: var(--medium);
}

.ConversationItem {
    background-color: var(--dark);
    width: 100%;
    height: 6em;
    display: -webkit-flex;
    display: flex;
    font-size: var(--s);
    position: relative;
}

.ConversationItem__Avatar {
    padding: 0.5em;
    height: 100%;
    -webkit-align-self: center;
            align-self: center;
    margin-right: 5px;
}

.ConversationItem__AvatarImage {
    border-radius: 50px;
    height: 3.5em;
}


.ConversationItem__Infos {
    height: 100%;
}

.ConversationItem__Name {
    text-align: left;
    letter-spacing: 0.5px;
    margin-top: 1em
}

.ConversationItem__Name h3 {
    font-size: var(--s);
    font-weight: 600;
}

.ConversationItem__Time {
    position: absolute;
    right: 10px;
    top: 10px;
    font-size: var(--xs);
}

.ConversationItem__Preview p {
    white-space: nowrap;
    font-size: var(--s);
    color: var(--light);
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 300px;
    margin-top: -0.7em;
    text-align: left;

}



.ConversationItem--active {
    background-color: var(--darkest);
    border-color: var(--darkest);

}

.ConversationItem__Writing .ConversationItem__Preview p {
 display: none !important;
}

.ConversationItem--online {
  border-color: var(--hilight);
}

.ConversationItem__UnreadCount {
    position: absolute;
    bottom: 16px;
    right: 10px;
    font-size: var(--s);
    height: 2em;
    width: 2em;
    color: var(--hilight);
    text-align: right;
    line-height: 25px;
}

@media screen and (max-width: 1500px) {
  .ConversationItem__Preview {
    max-width: 200px;
  }
}

@media screen and (max-width: 1150px) {
  .ConversationItem__Preview {
    max-width: 150px;
  }
}

@media screen and (max-width: 1050px) {
  .ConversationItem__Preview {
    max-width: 100px;
  }
}

@media screen and (max-width: 760px) {
  .ConversationItem__Preview {
    max-width: 190px;
  }
}



/* loading dots */
.ConversationItem__Writing::after {
    position: absolute;
    left: 75px;
    top: 48px;
    color: rgba(var(--highlight), 1);
    font-size: var(--l);
    content: '.';
    -webkit-animation: dots 1s steps(5, end) infinite;
            animation: dots 1s steps(5, end) infinite;
    display: block;
    height: 30px;
    width: calc(100% - 134px);
    text-align: left;
    line-height: 15px;
    background-color: var(--dark);
}

.ConversationItem--active .ConversationItem__Writing::after {
  background-color: var(--darkest)
}

@-webkit-keyframes dots {
  0%, 20% {
    color: rgba(var(--highlight), 0);
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 0),
      .5em 0 0 rgba(var(--highlight), 0);}
  40% {
    color: rgba(var(--highlight), 1);
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 0),
      .5em 0 0 rgba(var(--highlight), 0);}
  60% {
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 1),
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 1),
      .5em 0 0 rgba(var(--highlight), 1);}}

@keyframes dots {
  0%, 20% {
    color: rgba(var(--highlight), 0);
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 0),
      .5em 0 0 rgba(var(--highlight), 0);}
  40% {
    color: rgba(var(--highlight), 1);
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 0),
      .5em 0 0 rgba(var(--highlight), 0);}
  60% {
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 1),
      .5em 0 0 rgba(0,0,0,0);}
  80%, 100% {
    text-shadow:
      .25em 0 0 rgba(var(--highlight), 1),
      .5em 0 0 rgba(var(--highlight), 1);}}

.ConversationList {
  height: 100%;
  overflow-y: auto;
  background-color: var(--medium);

}

.PostItem {
    text-align: right;
}

.PostItem__Post {
    margin-top: 2rem;
    padding: 1rem;
    border-radius: 15px;
    margin-left: 0;
    box-sizing: border-box;
    max-width: 60%;
    margin-left: 0;
    box-sizing: border-box;
    max-width: 60%;
    display: -webkit-inline-flex !important;
    display: inline-flex !important;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

.PostItem__Post--me {
    display: inline-block;
    text-align: left;
    margin-left: 40%;
    background-color: var(--medium);
    border-radius: 10px 0 0 10px;

}

.PostItem__Post--other {
    text-align: left;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    background-color: var(--light);
    border-radius: 0 10px 10px 0;
    max-width: 80%;

}

.PostItem.PostItem--other {
    text-align: left;
}

.PostItem__Text {
    max-width: 95%;
    word-break: break-word;
    word-wrap: break-word;
}

.PostItem__Time {
    font-size: var(--xs);
    text-align: right;
    margin-bottom: -10px;
    margin-right: -7px;
    display: block;
    width: 100%;

}

.DateItem {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100px;
}

.DateItem__Time {
  font-size: var(--xs);
    text-align: center;
}

.ConversationHeader {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-flex: 1 1;
            flex: 1 1;
    height: 100%;
    padding-left: 80px;
    -webkit-align-items: center;
            align-items: center;
    box-sizing: border-box;
}

@media (min-width: 760px) {
  .ConversationHeader {
    padding-left: 15px;
  }
}



.ConversationHeader__Avatar {
    vertical-align: middle;
    width: 70px;
    padding: 0 10px 0 10px;
}

.ConversationHeader__AvatarImage {
    border-radius: 35px;
    width: 70px;
}

.ConversationHeader__Infos {
}

.ConversationHeader__Name {
  letter-spacing: 0.5px;
  font-size: var(--m);
}

.ConversationHeader__Status {
    display: none;
}

.Conversation {
    background-color: var(--medium);
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    overflow: hidden;

}

.Conversation__Header {
    height: 100px;
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
    background-color: var(--light);
    border-bottom: 2px solid var(--dark);
  }

.Conversation__Placeholder {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    background-color: var(--darkest);
}

.Conversation__PlaceholderText {
    font-size: var(--xl);
    color: var(--medium);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    margin-top: 50vh;
}

.Conversation__PostItems {
    -webkit-flex: 1 1 auto;
            flex: 1 1 auto;
    padding-bottom: 2em;
    overflow-y: auto;
    background-color: var(--darkest);
    border-bottom: 2px solid var(--dark);
}

.Conversation__Form {
    margin: 10px 10px 10px 10px;
    -webkit-align-self: center;
            align-self: center;
    width: 90%;

    border-radius: 10px;
    height: 5%;
    background-color: var(--medium);

    display: -webkit-flex;

    display: flex;
    -webkit-flex-flow: column nowrap;
            flex-flow: column nowrap;
    position: relative;
}

.Conversation__Form form {
    display: -webkit-flex;
    display: flex;
}

.Conversation__Write {
    -webkit-flex: 0 0 85%;
            flex: 0 0 85%;
    margin-top: 5px;
    padding: 5px;
    padding-left: 10px;

}

.Conversation__WriteTextarea {
    border: none;
    overflow: auto;
    outline: none;
    background: none;
    box-shadow: none;
    resize: none;
    width: 100%;
    color: var(--white);
    font-size: var(--s);
}

.Conversation__Send {
    color: white;
    font-weight: bold;
    right: 0px;
    position: absolute;
    padding: 5px 10px 5px 10px;
    margin-top: 5px;
    border-radius: 10px;
    border: 2px solid var(--darkest);
    background-color: var(--darkest);

}
.Conversation__Send:hover {
    color: white;
    background-color: var(--hilight);
    border: 2px solid var(--hilight)
}

.Conversation__SendInput {
    letter-spacing: 1px;
    border: none;
}

.Conversation__Writing {
    margin-top: 1em;

}

/* loading dots */
.Conversation__Writing::after {
    padding-left: 1.5em;
    font-size: var(--l);
    color: rgba(var(--highlight), 1);
    content: ' .';
    -webkit-animation: dots 1s steps(5, end) infinite;
            animation: dots 1s steps(5, end) infinite;
}

