.Menu {
  background-image: url(../../static/media/menu.4883d66b.svg);
  background-repeat: no-repeat;
  background-size: 200%;
  width: 50px;
  height: 50px;
  position: fixed;
  margin-left: 13px;
  margin-top: 13px;
  border: none;
}


@media (min-width: 1040px) {
  .Menu {
    display: none;
  }
}

